/* eslint import/no-webpack-loader-syntax: off */
import "./App.css";
import { useCallback, useRef, useState, useEffect } from "react"; // useMemo removed as it's replaced by useEffect/useRef pattern
import { useDropzone } from "react-dropzone";
import * as tf from "@tensorflow/tfjs";
import InferenceWorker from "workerize-loader!./inference.worker";
import { serializeTensor, deserializeTensor } from "./utils";

import * as THREE from 'three'; // Assuming THREE is installed or will be

// Basic Shader code (simplified, to be adapted from tiefling/shader.js)
const vertexShader = `
  attribute float depth;
  varying vec2 vUv;
  uniform vec2 mouseDelta;
  uniform float focus;
  uniform float meshDepth;
  uniform float sensitivity;

  void main() {
    vUv = uv;
    vec3 pos = position;

    float actualDepth = depth * meshDepth;
    float focusDepth = focus * meshDepth;

    // Rotational displacement (relative to focus depth)
    vec2 rotate = mouseDelta * sensitivity * 
        (1.0 - focus) * 
        (actualDepth - focusDepth) * 
        vec2(-1.0, 1.0);

    // Calculate edge proximity factor (0 at edges, 1 in center)
    float edgeWidth = 0.01; // controls edge stiffness
    vec2 edgeFactorVec = smoothstep(0.0, edgeWidth, vUv) * 
                        smoothstep(1.0, 1.0 - edgeWidth, vUv);
    float edgeFactor = edgeFactorVec.x * edgeFactorVec.y;

    // Apply displacement with edge preservation
    pos.xy += rotate * edgeFactor;

    gl_Position = projectionMatrix * modelViewMatrix * vec4(pos, 1.0);
  }
`;

const fragmentShader = `
  uniform sampler2D u_image;
  varying vec2 vUv;

  void main() {
    gl_FragColor = texture2D(u_image, vUv);
  }
`;

const WebGLViewer = ({ inputImage, depthMapImage, viewMode, mousePosition, orientation, parallaxStrength, focus, zoom }) => {
  const canvasRef = useRef(null);
  const rendererRef = useRef(null);
  const sceneRef = useRef(null);
  const cameraRef = useRef(null);
  const materialRef = useRef(null);
  const imageTextureRef = useRef(null);
  const depthTextureRef = useRef(null);
  const planeMeshRef = useRef(null);

  const handleResize = useCallback(() => {
    if (canvasRef.current && rendererRef.current && cameraRef.current && canvasRef.current.parentElement) {
        const parentElement = canvasRef.current.parentElement;
        const newWidth = parentElement.clientWidth;
        const newHeight = parentElement.clientHeight;
        rendererRef.current.setSize(newWidth, newHeight);
        cameraRef.current.aspect = newWidth / newHeight;
        cameraRef.current.updateProjectionMatrix();

        // Recalculate mesh scale if planeMeshRef.current exists
        if (planeMeshRef.current && imageTextureRef.current) {
            const imageAspect = imageTextureRef.current.image.width / imageTextureRef.current.image.height;
            const fov = cameraRef.current.fov;
            const cameraZ = cameraRef.current.position.z;
            const containerAspect = newWidth / newHeight;
            const visibleHeight = 2 * Math.tan(THREE.MathUtils.degToRad(fov / 2)) * cameraZ;
            const visibleWidth = visibleHeight * containerAspect;

            let scale;
            if (containerAspect > imageAspect) {
                scale = visibleHeight / 1; // Geometry height is 1
            } else {
                scale = visibleWidth / imageAspect; // Geometry width is imageAspect
            }
            planeMeshRef.current.scale.set(scale, scale, 1);
        }
    }
  }, []);

  useEffect(() => {
    // Initialize THREE.js scene
    const canvas = canvasRef.current;
    const renderer = new THREE.WebGLRenderer({ canvas, antialias: true });
    renderer.setSize(canvas.clientWidth, canvas.clientHeight);
    renderer.setPixelRatio(window.devicePixelRatio);
    rendererRef.current = renderer;

    const scene = new THREE.Scene();
    sceneRef.current = scene;

    const fov = 45;
    const camera = new THREE.PerspectiveCamera(fov, canvas.clientWidth / canvas.clientHeight, 0.1, 1000);
    camera.position.z = 2;
    cameraRef.current = camera;

    const geometry = new THREE.PlaneGeometry(1, 1);
    const material = new THREE.ShaderMaterial({
      vertexShader,
      fragmentShader,
      uniforms: {
        u_image: { value: null },
        mouseDelta: { value: new THREE.Vector2(0, 0) },
        focus: { value: focus },
        meshDepth: { value: 1.0 },
        sensitivity: { value: 0.5 }
      },
    });
    materialRef.current = material;

    // Plane will be created and added when depth map is ready
    // const plane = new THREE.Mesh(geometry, material);
    // scene.add(plane);

    const animate = () => {
      requestAnimationFrame(animate);
      renderer.render(scene, camera);
    };
    animate();

    window.addEventListener('resize', handleResize);
    handleResize();

    return () => {
      window.removeEventListener('resize', handleResize);
      if (rendererRef.current) rendererRef.current.dispose();
      if(imageTextureRef.current) imageTextureRef.current.dispose(); // Dispose textures on unmount
      if(depthTextureRef.current) depthTextureRef.current.dispose(); // Dispose textures on unmount
      // geometry.dispose(); material.dispose(); // If created here
    };
  }, [handleResize]);

  useEffect(() => {
    if (materialRef.current && inputImage) {
      if (imageTextureRef.current) imageTextureRef.current.dispose();
      const loader = new THREE.TextureLoader();
      loader.load(inputImage, (texture) => {
        imageTextureRef.current = texture;
        materialRef.current.uniforms.u_image.value = texture;
        // Resize logic will be handled when the mesh is created/updated
      });
    }
  }, [inputImage]);

  const createGeometryFromDepthMap = (depthData) => {
    const width = depthData.width;
    const height = depthData.height;
    const imageAspect = width / height;
    const geometry = new THREE.PlaneGeometry(imageAspect, 1, width - 1, height - 1);
    const vertices = geometry.attributes.position.array;
    const depths = new Float32Array(vertices.length / 3);

    for (let i = 0; i < vertices.length; i += 3) {
        const vertexIndex = i / 3;
        const u = geometry.attributes.uv.array[vertexIndex * 2];
        const v = 1.0 - geometry.attributes.uv.array[vertexIndex * 2 + 1];
        const x = Math.floor(u * (width - 1));
        const y = Math.floor(v * (height - 1));
        const pixelIndex = (y * width + x) * 4;
        const depthValue = depthData.data[pixelIndex] / 255.0;

        vertices[i + 2] = depthValue; // Set z based on depth
        depths[vertexIndex] = depthValue;
    }

    geometry.setAttribute('depth', new THREE.BufferAttribute(depths, 1));
    geometry.attributes.position.needsUpdate = true;
    return geometry;
  };

  useEffect(() => {
    if (planeMeshRef.current) {
      sceneRef.current.remove(planeMeshRef.current);
      planeMeshRef.current.geometry.dispose();
      planeMeshRef.current = null;
    }

    if (materialRef.current && depthMapImage && sceneRef.current) {
      const img = new Image();
      img.src = depthMapImage;
      img.onload = () => {
        const canvas = document.createElement('canvas');
        canvas.width = img.width;
        canvas.height = img.height;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0);
        const depthData = ctx.getImageData(0, 0, img.width, img.height);

        const geometry = createGeometryFromDepthMap(depthData);

        const mesh = new THREE.Mesh(geometry, materialRef.current);
        planeMeshRef.current = mesh;

        const fov = cameraRef.current.fov;
        const cameraZ = cameraRef.current.position.z;
        const containerAspect = canvasRef.current.clientWidth / canvasRef.current.clientHeight;
        const imageAspect = img.width / img.height;
        const visibleHeight = 2 * Math.tan(THREE.MathUtils.degToRad(fov / 2)) * cameraZ;
        const visibleWidth = visibleHeight * containerAspect;

        let scale;
        if (containerAspect > imageAspect) {
            scale = visibleHeight / 1; // Geometry height is 1
        } else {
            scale = visibleWidth / imageAspect; // Geometry width is imageAspect
        }
        mesh.scale.set(scale, scale, 1);

        sceneRef.current.add(mesh);
      };
    }
  }, [depthMapImage]);

  useEffect(() => {
    if (materialRef.current) {
      let targetX = 0, targetY = 0;
      if (viewMode === 'mouse' && mousePosition) {
        targetX = (mousePosition.x * 2 - 1);
        targetY = (mousePosition.y * 2 - 1);
      } else if (viewMode === 'gyro' && orientation) {
        targetX = (orientation.gamma / -30);
        targetY = (orientation.beta / -60);
      } else if (viewMode === 'gyroInverse' && orientation) {
        targetX = (orientation.gamma / 30);
        targetY = (orientation.beta / 60);
      }
      const easing = 0.05;
      const currentDelta = materialRef.current.uniforms.mouseDelta.value;
      currentDelta.x += (targetX - currentDelta.x) * easing;
      currentDelta.y += (targetY - currentDelta.y) * easing;
    }
  }, [mousePosition, orientation, viewMode]);

  useEffect(() => {
    if (materialRef.current) {
      materialRef.current.uniforms.focus.value = focus;
    }
  }, [focus]);

  useEffect(() => {
    if (materialRef.current) {
      materialRef.current.uniforms.sensitivity.value = parallaxStrength * 10; // Map strength to sensitivity
    }
  }, [parallaxStrength]);

  useEffect(() => {
    if (cameraRef.current) {
      cameraRef.current.position.z = 2 / zoom; // Adjust camera Z based on zoom
      cameraRef.current.updateProjectionMatrix();
    }
  }, [zoom]);

  return <canvas ref={canvasRef} className="main-view-canvas"></canvas>;
};

function App() {
  const inferenceWorkerRef = useRef(null); // Use useRef to hold the worker instance
  const [inputSource, setInputSource] = useState("");
  const [depthMapSource, setDepthMapSource] = useState(null);
  const [isDepthMapReady, setIsDepthMapReady] = useState(false);
  const inputRef = useRef(null);
  const outputRef = useRef(null);
  const [processing, setProcessing] = useState(false);
  const [isModelLoaded, setIsModelLoaded] = useState(false); // 添加模型加载状态
  const isMobileDevice = () => {
    return /Mobi|Android/i.test(navigator.userAgent);
  };

  const [viewMode, setViewMode] = useState(isMobileDevice() ? 'gyro' : 'mouse');
  const [mousePositionForShader, setMousePositionForShader] = useState({ x: 0.5, y: 0.5 });
  const [orientationForShader, setOrientationForShader] = useState(null);
  const webGLContainerRef = useRef(null);
  const [isGyroAvailable, setIsGyroAvailable] = useState(false);
  const [parallaxStrength, setParallaxStrength] = useState(0.025);
  const [focus, setFocus] = useState(0.75);
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const [zoom, setZoom] = useState(0.9); // New zoom state
  const [currentModel, setCurrentModel] = useState('depthAnything'); // Add currentModel state
  const [startX, setStartX] = useState(0); // For swipe to close drawer

  useEffect(() => {
    inferenceWorkerRef.current = new InferenceWorker(); // Initialize worker in useEffect
    // 检查模型加载状态
    const checkModelLoaded = async () => {
      try {
        // 调用worker的isModelLoaded方法检查模型是否加载完成
        const isLoaded = await inferenceWorkerRef.current.isModelLoaded(currentModel);
        setIsModelLoaded(isLoaded);
        if (!isLoaded) {
          // 如果模型未加载完成，每秒检查一次
          setTimeout(checkModelLoaded, 1000);
        }
      } catch (err) {
        console.error("Error checking model loaded status:", err);
        setTimeout(checkModelLoaded, 1000);
      }
    };
    
    checkModelLoaded();
    // Optional: Add cleanup for the worker if workerize-loader provides a terminate method
    // return () => { if (inferenceWorkerRef.current?.terminate) inferenceWorkerRef.current.terminate(); };
  }, []); // Empty dependency array ensures this runs only once on mount

  useEffect(() => {
    if (inferenceWorkerRef.current) {
      setIsModelLoaded(false); // 切换模型时重置加载状态
      inferenceWorkerRef.current.switchModel(currentModel)
        .then(success => {
          if (success) {
            // 检查模型加载状态
            const checkModelLoaded = async () => {
              try {
                const isLoaded = await inferenceWorkerRef.current.isModelLoaded(currentModel);
                setIsModelLoaded(isLoaded);
                if (!isLoaded) {
                  setTimeout(checkModelLoaded, 1000);
                } else if (inputSource && !processing) {
                  // 模型加载完成且有输入图像时，重新运行推理
                  inferCallback();
                }
              } catch (err) {
                console.error("Error checking model loaded status:", err);
                setTimeout(checkModelLoaded, 1000);
              }
            };
            checkModelLoaded();
          }
        });
    }
  }, [currentModel]); // Switch model in worker when currentModel changes

  useEffect(() => {
    if (window.DeviceOrientationEvent) {
      setIsGyroAvailable(true);
      const handleDeviceOrientation = (event) => {
        if (viewMode === 'gyro' || viewMode === 'gyroInverse') {
          setOrientationForShader({
            alpha: event.alpha, // Z axis rotation
            beta: event.beta,   // X axis rotation (front to back tilt)
            gamma: event.gamma, // Y axis rotation (side to side tilt)
          });
        }
      };
      window.addEventListener('deviceorientation', handleDeviceOrientation);
      return () => {
        window.removeEventListener('deviceorientation', handleDeviceOrientation);
      };
    } else {
      setIsGyroAvailable(false);
    }
  }, [viewMode]); // Rerun if viewMode changes to attach/detach listener

  const inferCallback = useCallback(async () => { // Made the callback async directly
    if (!inferenceWorkerRef.current) {
      console.error("Inference worker is not initialized yet.");
      alert("Worker not ready, please try again shortly.");
      return;
    }
    
    // 检查模型是否已加载
    if (!isModelLoaded) {
      console.error("Model is not loaded yet.");
      alert("模型尚未加载完成，请稍后再试。");
      return;
    }
    
    // Ensure inputRef.current is available and has a valid src and dimensions before processing
    if (processing || !inputRef.current || !inputRef.current.src || inputRef.current.src === "" || inputRef.current.naturalWidth === 0 || inputRef.current.naturalHeight === 0) {
      return;
    }
    setProcessing(true);
    // Clear previous depth map on canvas before new inference
    if (outputRef.current) {
        const ctx = outputRef.current.getContext('2d');
        ctx.clearRect(0, 0, outputRef.current.width, outputRef.current.height);
    }
    setDepthMapSource(null); // Explicitly clear depth map source for WebGLViewer
    setIsDepthMapReady(false); // Set depth map not ready

    try {
      let input = tf.browser.fromPixels(inputRef.current);
      const originalSize = [input.shape[0], input.shape[1]];
      // Ensure input has 3 channels for resizeBilinear
      if (input.shape[2] === 4) {
        input = input.slice([0, 0, 0], [input.shape[0], input.shape[1], 3]);
      }
      
      const inputSerialized = serializeTensor(input);
      input.dispose(); 

      let outputTensor = deserializeTensor(
        await inferenceWorkerRef.current.infer(inputSerialized) // Use worker from ref
      );
      outputTensor = tf.image.resizeBilinear(outputTensor, originalSize);
      
      // Draw to outputRef (2D canvas preview)
      if (outputRef.current) {
        outputRef.current.width = originalSize[1];
        outputRef.current.height = originalSize[0];
        await tf.browser.toPixels(outputTensor, outputRef.current);
      }
      
      // Create data URL for WebGLViewer
      const tempCanvas = document.createElement('canvas');
      tempCanvas.width = originalSize[1];
      tempCanvas.height = originalSize[0];
      await tf.browser.toPixels(outputTensor.clone(), tempCanvas); // Clone tensor before using for toDataURL if needed elsewhere
      setDepthMapSource(tempCanvas.toDataURL());
      setIsDepthMapReady(true);
      
      outputTensor.dispose();

    } catch (err) {
      console.error("Inference error:", err);
      alert("An unknown error occurred during inference.");
    } finally {
      setProcessing(false); 
    }
  }, [processing, inputRef, outputRef, setDepthMapSource, setProcessing, currentModel, isModelLoaded]); // Added isModelLoaded to deps

  const onDrop = useCallback(
    (acceptedFiles) => {
      if (processing || !isModelLoaded) {
        return;
      }
      const fr = new FileReader();
      fr.onload = () => {
        setInputSource(fr.result); // This will trigger img.onLoad, which calls inferCallback
        // Clearing depthMapSource here is good, inferCallback will also clear its canvas part
        setDepthMapSource(null);
        setIsDepthMapReady(false);
        if (outputRef.current) { // Clear the 2D preview canvas as well
            const ctx = outputRef.current.getContext('2d');
            if (outputRef.current.width > 0 && outputRef.current.height > 0) {
                 ctx.clearRect(0, 0, outputRef.current.width, outputRef.current.height);
            }
        }
      };
      fr.readAsDataURL(acceptedFiles[0]);
    },
    [setInputSource, setDepthMapSource, processing, outputRef, isModelLoaded] // Added isModelLoaded
  );
  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: "image/*",
  });

  const handleMouseMoveInViewer = (event) => {
    if (viewMode === 'mouse' && webGLContainerRef.current) {
      const rect = webGLContainerRef.current.getBoundingClientRect();
      const clientX = event.touches ? event.touches[0].clientX : event.clientX;
      const clientY = event.touches ? event.touches[0].clientY : event.clientY;
      const x = (clientX - rect.left) / rect.width;
      const y = (clientY - rect.top) / rect.height;
      setMousePositionForShader({ x: Math.max(0, Math.min(1, x)), y: Math.max(0, Math.min(1, 1.0 - y)) });
    }
  };

  const toggleDrawer = () => {
    setIsDrawerOpen(!isDrawerOpen);
  };

  const handleTouchStart = (e) => {
    setStartX(e.touches[0].clientX);
  };

  const handleTouchMove = (e) => {
    if (isDrawerOpen) {
      const currentX = e.touches[0].clientX;
      const diffX = currentX - startX;
      // If swiping left significantly, close the drawer
      if (diffX < -50) { // Threshold of 50 pixels
        setIsDrawerOpen(false);
      }
    }
  };

  return (
    <div className="app-container">
      <button className="hamburger-menu" onClick={toggleDrawer}>
        <span></span>
        <span></span>
        <span></span>
      </button>
      {isDrawerOpen && <div className="overlay" onClick={toggleDrawer}></div>}
      <aside
        className={`sidebar ${isDrawerOpen ? 'sidebar--open' : ''}`}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
      >
        <h2>控制台</h2>
        <div className="control-group">
          <label htmlFor="modelSelect">选择模型</label>
          <select id="modelSelect" value={currentModel} onChange={(e) => setCurrentModel(e.target.value)}>
            <option value="depthAnything">DepthAnything</option>
            <option value="midas">Midas</option>
          </select>

          <label htmlFor="viewModeSelect">控制模式</label>
          <select id="viewModeSelect" value={viewMode} onChange={(e) => setViewMode(e.target.value)}>
            <option value="mouse">鼠标</option>
            {isGyroAvailable && <option value="gyro">陀螺仪</option>}
            {isGyroAvailable && <option value="gyroInverse">陀螺仪(反向)</option>}
          </select>
          {!isGyroAvailable && (viewMode === 'gyro' || viewMode === 'gyroInverse') && <p style={{color: 'orange'}}>陀螺仪不可用或不支持。</p>}
        </div>
        
        <div className="control-group">
          <label htmlFor="parallaxStrengthRange">位移强度: {parallaxStrength.toFixed(3)}</label>
          <input 
              type="range" 
              id="parallaxStrengthRange" 
              min="0.001" 
              max="0.1" 
              step="0.001" 
              value={parallaxStrength} 
              onChange={(e) => setParallaxStrength(parseFloat(e.target.value))} 
              onTouchMove={(e) => e.stopPropagation()} // Prevent sidebar from closing when sliding
            />
        </div>

        <div className="control-group">
          <label htmlFor="focusRange">焦点: {focus.toFixed(2)}</label>
          <input 
              type="range" 
              id="focusRange" 
              min="0.0" 
              max="1.0" 
              step="0.01" 
              value={focus} 
              onChange={(e) => setFocus(parseFloat(e.target.value))} 
              onTouchMove={(e) => e.stopPropagation()} // Prevent sidebar from closing when sliding
            />
        </div>

        <div className="image-preview-area">
            <h3>原始图像</h3>
            <div 
              className={[
                "image-dropzone",
                !isModelLoaded
                  ? "image-dropzone--disabled"
                  : isDragActive
                    ? "image-dropzone--highlight"
                    : "image-dropzone--default",
              ].join(" ")}
              {...(isModelLoaded ? getRootProps() : {})}
            >
              {isModelLoaded && <input {...getInputProps()} />}
              {inputSource ? (
                <img ref={inputRef} alt="Input" src={inputSource} className="preview-image" onLoad={isModelLoaded ? inferCallback : undefined} />
              ) : (
                <p>{!isModelLoaded ? "模型加载中..." : "点击上传，或拖拽图片至此处"}</p>
              )}
            </div>
        </div>

        <div className="image-preview-area">
            <h3>深度图</h3>
            <canvas ref={outputRef} className="preview-image"></canvas>
        </div>
        
        <button onClick={inferCallback} disabled={processing || !inputSource || !isModelLoaded}>
          {processing ? "生成中..." : !isModelLoaded ? "模型加载中..." : "生成深度图"}
        </button>
        <div className="made-by-text">
          Made by J
        </div>
      </aside>
      <main
        className="main-content"
        onMouseMove={handleMouseMoveInViewer}
        onTouchMove={handleMouseMoveInViewer}
        ref={webGLContainerRef}
      >
        <h1 className="main-title">单目深度图估算</h1>
        <div className="webgl-viewer-container">
          {processing && !isDepthMapReady && (
            <div className="loading-overlay">
              <p>生成中...</p>
            </div>
          )}
          <WebGLViewer 
            inputImage={inputSource} 
            depthMapImage={isDepthMapReady ? depthMapSource : null} 
            viewMode={viewMode} 
            mousePosition={mousePositionForShader}
            orientation={orientationForShader}
            parallaxStrength={parallaxStrength}
            focus={focus}
            zoom={zoom} />
          <div className="viewer-controls">
            <button onClick={() => {
              setZoom(0.9)
              setMousePositionForShader({ x: 0.5, y: 0.5 });
            }}>重置</button>
            <label htmlFor="zoomRange">{zoom.toFixed(2)}x</label>
            <input 
              type="range" 
              id="zoomRange" 
              min="0.1" 
              max="1.5" 
              step="0.1" 
              value={zoom} 
              onChange={(e) => setZoom(parseFloat(e.target.value))} 
            />
          </div>
        </div>
      </main>
    </div>
  );
}

export default App;
